@media  (min-width:1025px) and (max-width:1200px){
    *{  background-color:aquamarine;
    font-family:Georgia, 'Times New Roman', Times, serif;
    text-align:justify;
    margin-left:5%;
    margin-top:5%;
    font-size:20px;
    font-style:oblique;
    box-sizing:border-box;
}
}
form{
    margin-right:200px;
}
legend{
    font-size:30px;
    color:crimson; 
}
fieldset{
    margin-left:-50px;
    margin-right:200px;
    line-height:20px;
    border-radius:5px;
}
.text{
    width:350px;
    height:33px;
}
.forall{
    margin-left:18px;
    border-radius:5px;
    font-size:15px;
}
polygon{
    fill:lime;
    stroke:black;
    stroke-width:4px;
    fill-rule:evenodd;
    opacity:0.7;
}
ellipse{
    opacity:0.7;
}
button{
    margin-left:150px;
    color:brown;
    background-color:aqua;
    border-radius:5px;
}
button:hover{
    background-color:aquamarine;
    border-color:red;
    cursor:pointer;
}
.high{
    margin-left:-70px;
    width:80%;
}
option{
    font-size:14px;}
.over{fill:#00007f}
@media  screen and (max-width:768px){
    *{ background-color:bisque; 
        margin-left:7%;
        font-size:15px;
        text-align:justify;
    }
    .form{
        width:100%;
        font-size:10px;
    }
    .forall{
            margin-left:0px;
            border-radius:4px;
            font-size:20px;}
            label{
                font-size:18px;;
            }
            .text{
                width:320px;
                height:33px;}
                fieldset{
                    margin-left:-110px;
                    margin-right:10px;
                    line-height:50px;
                    border-radius:5px;
                    padding-right:-20px;
                padding-left:20px;
            padding-top:10px;
        padding-bottom:50px;}
        button{
            font-style:oblique;
            font-size:22px;
            width:90px;
            height:40px;
            margin-left:100px;
            color:brown;
            background-color:aqua;
            border-radius:5px;
        }
        #some{
            font-size:20px;
            font-family:sans-serif;
            margin-top:40px;
        }
        .forall{
            margin-left:20px;
        }
        .high{
            margin-left:-70px;
            width:120%;
        }
    }
